label
  color #373737

.lds-ripple
  display inline-block
  position relative
  width 128px
  height 128px

.lds-ripple div
  position absolute
  border 4px solid #2c3e50
  opacity 1
  border-radius 50%
  animation lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite

.lds-ripple div:nth-child(2)
  animation-delay -0.5s

@keyframes lds-ripple
  0%
    top 64px
    left 64px
    width 0
    height 0
    opacity 1

  100%
    top 0
    left 0
    width 128px
    height 128px
    opacity 0
</style>